import React from 'react'
import Footer from '../components/Footer'
import Navbar from '../components/Navbar'

function Contact() {
    return (
        <>
            <Navbar />
            <div className='container py-5'>
                <div className='row my-5 g-4'>
                    {/* Left Side - Contact Info */}
                    <div className='col-lg-4 col-md-5 col-sm-12 bg-white shadow p-4'>
                        <div className='d-flex align-items-center mb-3'>
                            <img src={`${process.env.PUBLIC_URL}/assets/icons-phone.png`} alt="Phone Icon" />
                            <h4 className='ms-3'>Call To Us</h4>
                        </div>
                        <p>We are available 24/7, 7 days a week.</p>
                        <p className='fw-bold'>Phone: +8801611112222</p>
                        <hr />
                        <div className='d-flex align-items-center mb-3'>
                            <img src={`${process.env.PUBLIC_URL}/assets/icons-mail.png`} alt="Mail Icon" />
                            <h4 className='ms-3'>Write To Us</h4>
                        </div>
                        <p>Fill out our form and we will contact you within 24 hours.</p>
                        <p className='fw-bold'>customer@exclusive.com</p>
                        <p className='fw-bold'>support@exclusive.com</p>
                    </div>

                    {/* Right Side - Contact Form */}
                    <div className='col-lg-8 col-md-7 col-sm-12 shadow p-4'>
                        <form className="row g-3">
                            <div className="col-12 col-md-4">
                                <input type="text" className="form-control bg-light" placeholder="Your Name *" />
                            </div>
                            <div className="col-12 col-md-4">
                                <input type="email" className="form-control bg-light" placeholder="Your Email *" />
                            </div>
                            <div className="col-12 col-md-4">
                                <input type="text" className="form-control bg-light" placeholder="Your Phone *" />
                            </div>
                            <div className="col-12">
                                <textarea className="form-control bg-light" rows="5" placeholder="Your Message"></textarea>
                            </div>
                            <div className="col-12 text-end">
                                <button className="btn-1  px-4" type="submit">Send Message</button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
            <Footer />
        </>
    )
}

export default Contact
