import React from 'react'
import { Link } from 'react-router-dom'


function Footer() {
    return (
        <footer className="footer text-white mt-auto">
            <div className="container">
                <div className="row row-cols-1 row-cols-sm-2 row-cols-md-3 row-cols-lg-5 py-5 text-center text-lg-start">
                    <div className="col mb-4">
                        <ul className="list-unstyled">
                            <li className="fw-bold mb-2 fs-5">Exclusive</li>
                            <li className="mb-2">Subscribe</li>
                            <li className="mb-2">Get 10% off your first order</li>
                            <input
                                placeholder="Enter your email"
                                className="border bg-dark text-white form-control"
                            />
                        </ul>
                    </div>

                    <div className="col mb-4">
                        <ul className="list-unstyled">
                            <li className="fw-bold mb-3 fs-5">Support</li>
                            <li className="mb-2">111 Bijoy Sarani, Dhaka, Bangladesh.</li>
                            <li className="mb-2">exclusive@gmail.com</li>
                            <li className="mb-2">+88015-88888-9999</li>
                        </ul>
                    </div>

                    <div className="col mb-4">
                        <ul className="list-unstyled">
                            <li className="fw-bold mb-3 fs-5">Account</li>
                            <li className="mb-2">
                                <Link className="text-decoration-none text-white" to="/account">
                                    My Account
                                </Link>
                            </li>
                            <li className="mb-2">
                                <Link className="text-decoration-none text-white" to="/login">
                                    Login
                                </Link>
                               
                                <Link className="text-decoration-none text-white" to="/register">
                                    Register
                                </Link>
                            </li>
                            <li className="mb-2">
                                <Link className="text-decoration-none text-white" to="/cart">
                                    Cart
                                </Link>
                            </li>
                        </ul>
                    </div>

                    <div className="col mb-4">
                        <ul className="list-unstyled">
                            <li className="fw-bold fs-5 mb-3">Quick Link</li>
                            <li className="mb-2">Privacy Policy</li>
                            <li className="mb-2">FAQ</li>
                            <li className="mb-2">
                                <Link className="text-decoration-none text-white" to="/contact">
                                    Contact
                                </Link>
                            </li>
                        </ul>
                    </div>

                    <div className="col mb-4">
                        <ul className="list-unstyled">
                            <li className="fw-bold fs-5 mb-3">Download App</li>
                            <div className="d-flex flex-column align-items-center align-items-lg-start">
                                <img
                                    src={`${process.env.PUBLIC_URL}/assets/Qrcode 1.png`}
                                    className="img-fluid mb-2"
                                    alt="QR Code"
                                />
                                <img
                                    src={`${process.env.PUBLIC_URL}/assets/png-transparent-google-play-.png`}
                                    className="img-fluid mb-2"
                                    alt="Google Play"
                                />
                                <img
                                    src={`${process.env.PUBLIC_URL}/assets/AppStore.png`}
                                    className="img-fluid"
                                    alt="App Store"
                                />
                            </div>
                        </ul>
                    </div>
                </div>

                <div className="row">
                    <div className="col-12 text-light text-center py-4 my-4">
                        © Rimel 2022. All Rights Reserved.
                    </div>
                </div>
            </div>
        </footer>

    )
}

export default Footer