import React from 'react';
import { useParams } from 'react-router-dom';
import { useSelector } from 'react-redux';
import Navbar from '../components/Navbar';
import Footer from '../components/Footer';
import useAddToCart from "../components/handle authontication.js";
import useAddTowishlist from '../components/wishlist-auth.js';

function Productdetails() {
    const addToCart = useAddToCart();
    const addToWishlist = useAddTowishlist();
    const { matchedid } = useParams();

    const products = useSelector(state => state.products.products);
    console.log("Matched ID:", matchedid);
    console.log("All Products:", products);

    if (!products || products.length === 0) {
        return <h2 className="text-center my-5">Loading products...</h2>;
    }

    const product = products.find((item) => item.id === parseInt(matchedid));
    console.log(product);

    if (!product) {
        return <h2 className="text-center my-5">Product not found</h2>;
    }

    const roundedRating = Math.round(product.rating);

    return (
        <>
            <Navbar />
            <div className='container py-5'>
                <div className='row g-4'>


                    <div className='col-lg-6 col-md-12 d-flex flex-column align-items-center'>
                        <div className='row g-2'>

                            <div className='col-3 d-flex flex-column gap-3'>
                                {product.images.slice(1).map((item, index) => (
                                    <img
                                        key={index}
                                        src={item}
                                        alt={`Thumbnail ${index + 1}`}
                                        className='img-fluid rounded shadow-sm'
                                        style={{ cursor: 'pointer', height: '100px', objectFit: 'cover' }}
                                    />
                                ))}
                            </div>

                            {/* Main Image */}
                            <div className='col-9'>
                                <img
                                    src={product.images[0]}
                                    alt='Main Product'
                                    className='img-fluid rounded shadow w-100'
                                />
                            </div>
                        </div>
                    </div>

                    {/* Product Details */}
                    <div className='col-lg-6 col-md-12'>
                        <h2 className="fw-bold">{product.title}</h2>

                        {/* Rating */}
                        <div className="stars d-flex align-items-center">
                            {Array.from({ length: 5 }).map((_, index) => (
                                <span key={index} style={{ color: index < roundedRating ? "gold" : "gray", fontSize: '20px' }}>
                                    ★
                                </span>
                            ))}
                            <span className="ms-2">({product.reviews.length} Reviews) | {product.availabilityStatus}</span>
                        </div>

                        {/* Price */}
                        <div className='my-3'>
                            <span className="text-decoration-line-through fw-semibold text-muted fs-5">${Math.floor(product.price)}</span>
                            <span className='ms-3 fw-semibold fs-4 text-danger'>${Math.floor(product.price - (product.price * product.discountPercentage / 100))}</span>
                        </div>

                        {/* Description */}
                        <p>{product.description}</p>
                        <hr />

                        {/* Buttons */}
                        <div className='d-flex flex-wrap gap-3'>
                            <button onClick={() => addToCart(product)} className='btn-1  px-4 py-2'>
                                Buy Now
                            </button>
                            <button
                                onClick={() => addToWishlist(product)}
                                className='btn border px-3 py-2'
                                style={{ cursor: "pointer" }}
                            >
                                <i className="fa-solid fa-heart"></i>
                            </button>
                        </div>

                        {/* Delivery & Return Info */}
                        <div className='border mt-4 p-3 rounded'>
                            <div className='d-flex align-items-center mb-3'>
                                <i className="fa-solid fa-truck fs-2"></i>
                                <div className="ms-3">
                                    <h5>Free Delivery</h5>
                                    <p>Enter your postal code for Delivery Availability</p>
                                </div>
                            </div>

                            <hr />

                            <div className='d-flex align-items-center'>
                                <i className="fa-solid fa-right-left fs-2"></i>
                                <div className="ms-3">
                                    <h5>Return Delivery</h5>
                                    <p>Free 30 Days Delivery Returns. Details </p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <Footer />
        </>
    );
}

export default Productdetails;
